import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "../styles/components/carrosel.css"

const Carrosel = () => {
  let settings = {
    dots: true,
    infinite: false,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 900,
        settings: {
          slidesToShow: 2,
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          arrows: false
        }
      }
    ]

  };
  return (
    <Slider {...settings}>
      <div className="modulo0">
        <h3>Mdulo 0 - Introduo ao Curso</h3>
        <ul>
          <li>
            Quem somos?
          </li>
          <li>
            O que  o curso de Whatsapp Business.
          </li>
        </ul>
      </div>
      <div className="modulo">
        <h3>Mdulo 1 - Instalando o Whatsapp Business</h3>
        <ul>
          <li>
            Instalao
          </li>
        </ul>
      </div>
      <div className="modulo">
        <h3>Mdulo 2 - Configuraes do seu perfil Whatsapp Business</h3>
        <ul>
          <li>
            Configurando seu perfil
          </li>
          <li>
            Como usar links de contato
          </li>
        </ul>
      </div>
      <div className="modulo">
        <h3>Mdulo 3 - Mostrando sua cara! Compartilhando o que passa na sua empresa.</h3>
        <ul>
          <li>
            O que  o Status?
          </li>
          <li>
            Como compartilhar seu contedo
          </li>
        </ul>
      </div>
      <div className="modulo">
        <h3>Mdulo 4 - Automatizando seu Whatsapp Business</h3>
        <ul>
          <li>Etiquetas</li>
          <li>Mensagens de Ausncia e Boas Vindas</li>
          <li>Respostas Rpidas</li>
        </ul>

      </div>
      <div className="modulo">
        <h3>Mdulo 5 - Compartilhando seu produto</h3>
        <ul>
          <li> Listas de transmisso </li>
          <li>Grupos</li>
          <li>Vinculando Contas</li>
        </ul>
      </div>
      <div className="modulo">
        <h3>Mdulo 6 - Catlogo do Whatsapp Business</h3>
        <ul>
          <li> Desenvolvendo seu Catlogo</li>
          <li>Enviando Catlogo e Produtos ao Cliente</li>
        </ul>
      </div>
      <div className="modulo">
        <h3>Mdulo 7 - Segurana e privacidade</h3>
        <ul>
          <li> TFA - Two Factor Authentication ou Autenticao de dois fatores</li>
          <li>Configuraes de privacidade</li>
        </ul>
      </div>
      <div className="modulo">
        <h3>Mdulo 8 - Dando uma cara para sua empresa </h3>
        <ul>
          <li> Introduo ao Canva e criao de pequenas artes.</li>
        </ul>
      </div>
    </Slider>
  );
}

export default Carrosel;